.giraffe {
	
	--giraffe-skin: black;
	--giraffe-skin: yellow;
	
	position: relative;
	margin: auto;
	display: block;
	margin-top: 5%;
	width: 500px;
	height: 500px;
}

.giraffe-bottom {
	top: 50%;
	left: 40%;
	width: 45%;
	height: 20%;
	border-radius: 50% 50% 100% 100%;
	background: var(--giraffe-skin, yellow);
}

.giraffe * {
	position: absolute;
}